import {apishka, ws} from '../Api';

import {NotificationManager} from 'react-notifications';	

	export function getData(x) {

		this.setState({loading:true},()=>{
			let print = false
			let pagenum = this.state.pagenum
			let pagesize = this.state.pagesize
			let filters = this.state.paramfilters
			let colsOrder = this.state.colsOrder
			let colsSize = this.state.colsSize
			apishka('GET', {}, {}, '/api/getusersettings', (usersettings) => {
				usersettings = usersettings.outjson
				if (usersettings[this.state.path]) {
					if (usersettings[this.state.path].pagination) {
						if (pagenum === 1 && pagesize === 15 )
							pagesize = usersettings[this.state.path].pagination.pagesize	
					}
					/*if (Object.keys(filters).length === 0 && usersettings[this.state.path].filters)
						filters = usersettings[this.state.path].filters*/
					
					if (usersettings[this.state.path].colsOrder)
						colsOrder = usersettings[this.state.path].colsOrder
					
					if (usersettings[this.state.path].colsSize)
						colsSize = usersettings[this.state.path].colsSize					
				}
				usersettings[this.state.path] = {pagination:{pagesize:pagesize}, filters:filters,colsOrder:colsOrder, colsSize:colsSize}
				this.setState({pagesize:pagesize, colsOrder:colsOrder, colsSize:colsSize, usersettings:usersettings})	
				apishka(
					'post', 
					{ settings:usersettings},
					{},
					'/api/saveusersettings',
					(i) => console.log(i),
					(err) => NotificationManager.error(err.response.data.message)
				)
				apishka('post', { pagination:{pagenum:pagenum, pagesize:pagesize}, 
						filters:filters,
						inputs: this.state.inputs ,
						print:print					
					}, {path:this.state.path}, '/schema/list', (data) => {
						let filteredConfig = data.config.filter((x) => x.visible)

						filteredConfig.unshift({col:"rownum",title:"rownum",type:"rownum"})
						filteredConfig.push({col:"...",title:"...",type:"acts"})
						let tempConf = []
						if (this.state.colsOrder && this.state.colsOrder.length > 0) {
							this.state.colsOrder.forEach((x) => {
								if (filteredConfig.filter((item)=>x.columnKey === item.title)[0])
									tempConf.push(filteredConfig.filter((item)=>x.columnKey === item.title)[0])
							})
						}	
						
						filteredConfig.forEach((x) => {
							
							if (!this.state.colsOrder.filter((item) => item.columnKey === x.title)[0]) {
								console.log('X;',x)
								tempConf.push(x)
							}
						})
						
						console.log("TEMP",tempConf)
						
						filteredConfig = [...tempConf]
						
						if (filteredConfig.filter((x) => x.type === "acts").length === 0 )
							filteredConfig.push({col:"...",title:"...",type:"acts"})
						if (data.checker) {
							filteredConfig.unshift({col:"",title:"",type:"checker"})
						}
						

							
						//console.log(this.state.colsSize, this.state.colsOrder, "FILTERED:",filteredConfig)
						filteredConfig.forEach((x) => x.width = (this.state.colsSize[x.title] || x.width) )
						
						this.setState({
							data:data.data,
							config:data.config, 
							filteredConfig:filteredConfig, 
							filters:data.filters,
							pagination:data.pagination, 
							table:data.table,
							loading:false,
							acts:data.acts,
							pagecount:parseInt(data.foundcount/this.state.pagesize),
							foundcount:data.foundcount,
							isorderby:data.isorderby,
							ispagecount:data.ispagecount,
							ispagesize:data.ispagesize,
							isfoundcount:data.isfoundcount,
							checker:data.checker,
							viewid:data.viewid,
							subscrible:data.subscrible,
							user:data.user,
							classname:data.classname,
							print:print,
							title:data.title,
							checked:[]
						},() => {
							document.title = this.state.title
							if (data.subscrible && !this.state.socket && !print) {
								let socket = new WebSocket(ws);
								socket.onopen = () => {
									this.setState({socket:true}, () => 
										this.sendSocket(socket)
									)
								};

								socket.onclose = (event) => {
								  if (event.wasClean) {
									console.log('clear closed');
								  } else {
									NotificationManager.error('failed');
								  }
								  NotificationManager.error('code: ' + event.code + ' reason: ' + event.reason);
								};

								socket.onmessage = (e) => {
								  let data = JSON.parse(e.data)	
								  if (!data.error) {
									  data.forEach((x) => {
										  NotificationManager.info(x.notificationtext)
										  apishka('post', {id:x.id}, {}, '/api/setsended', (data) => {},(err) =>{})
									  })
									  this.getData()
									 
								  }	else {
									  NotificationManager.error(data.error)
								  }
								};

								socket.onerror = (error) => {
								  NotificationManager.error("error " + error.message);
								};
							}
							
							if (print)
								window.print()	
						})
					}, (err) => this.setState({loading:false},() => NotificationManager.error(err.response.data.message))) 
				
		}, (err) => this.setState({loading:false},() => NotificationManager.error(err.response.data.message)) ) }) 
				
	}
		
	export function onFilterChange(col, val){
		//console.log(val)
		let paramfilters = this.state.paramfilters;
		paramfilters[col] = val 
		this.setState({paramfilters:paramfilters},()=>{
			this.getData()
		})
	}
	
	export function onFilterChangeDate(col, val, dt){
		let paramfilters = this.state.paramfilters;
		if (!paramfilters[col]) {
			paramfilters[col] = {}
		}
		if (!val) {
			paramfilters[col][dt] = null
		} else {
			paramfilters[col][dt] = val
		}
		
		this.setState({paramfilters:paramfilters},() => {
			if ((this.state.paramfilters[col]['date1'] && this.state.paramfilters[col]['date2']) || 
				(!this.state.paramfilters[col]['date1'] && !this.state.paramfilters[col]['date2'])) {
				this.getData()
			}  
		})
	}
		
	export function getTable(col,table) {
		let filters = this.state.filters
		let tabclmns = this.state.config.filter((obj) => obj.col === col)
		let tabcl
		if (!tabclmns[0].multiselecttable)
			tabcl = tabclmns[0].relationcolums 
		else {	
			tabcl = tabclmns[0].multicolums 	
			table = tabclmns[0].multiselecttable
		}
		let tablecolums = ""
		for (let i in tabcl) {
			tablecolums += tabcl[i].value + '+'
		}
		tablecolums = tablecolums.substring(0,tablecolums.length - 1)
		
		this.setState({loading:true},()=>{
			if (filters.filter((itm) => itm.col === col && !itm.data)) {
				
				apishka('get', {}, {tabname:table, tabcolums:tablecolums}, '/api/gettable', 
					(data)=>{
						data = data.outjson 
						this.setState({loading:false})
						let f = {}
						data.unshift({label:"*", value:null})
						f[col] = data
						this.setState({kfilters:f})
					}, 
					(err) => this.setState({loading:false},() => NotificationManager.error(err.message)))

			} else {
				this.setState({loading:false})
			}	
		})
	}
	
	export function pagination(type){
		if (type === 'next') {
			if (this.state.pagenum <= this.state.pagecount) {
				this.setState({pagenum:this.state.pagenum + 1},()=>this.getData())
			}
		} else {
			if (this.state.pagenum!==1) {
				this.setState({pagenum:this.state.pagenum - 1},()=>this.getData())
			}
		}
	}
	
	export function delRow(id, roles) {
		if (window.confirm('Confirm delete!')){ 
			this.setState({loading:true},()=>{
				apishka('delete', {  
					tablename: this.state.table,
					id:id,
					viewid:this.state.viewid		
				}, {}, '/api/deleterow', (data)=>{
					this.setState({
						loading:false
					},() => this.getData())
					}, (err)=>{
						this.setState({loading:false},() => {
								if (err.response && err.response.data) {
									NotificationManager.error(err.response.data.message)
								}
								else {
									NotificationManager.error('unknown error!')
								}	
						})	
					})	
			})
		}
	}
	
	export function orderBy(col, t ,related, fn, fncols) {
		let inputs = this.state.inputs || {}
		let orderby = []
		if (inputs) {
			if (inputs.orderby)
				orderby = inputs.orderby
		}
		if (orderby.filter((x) => x.col === col && x.t === t && x.desc.length === 0).length>0) {
			orderby = orderby.filter((x) => x.col !== col && x.t !== t )
			orderby.push({"col":col,"t":t,"related":related || 0, "desc":"desc", "over":true, "fn":fn, "fncols":fncols})
		}	
		else {
			if (orderby.filter((x) => x.col === col && x.over).length === 0) {
				orderby = orderby.filter((x) => x.col !== col && x.t !== t )
				orderby.push({"col":col,"t":t,"related":related || 0, "desc":"", "fn":fn, "fncols":fncols})
			}
			else
				orderby = orderby.filter((x) => x.col !== col && x.t !== t )
			
		}

		inputs['orderby'] = orderby
		this.setState({inputs:inputs},() => this.getData())
	}
	
	export function orderByDel(col, t) {
		let inputs = this.state.inputs
		let orderby = inputs['orderby'] || []
		orderby = orderby.filter((x) => x.col !== col && x.t !== t)
		inputs['orderby'] = orderby
		this.setState({inputs:inputs},() => this.getData())
	}
	
	export function setChecked(item){
		/*
			push item into checked array
			checked is global variable			
		*/
		let checked = this.state.checked
		let config = this.state.config
		let id = config.filter((x) => x.col === 'id')[0].title
		checked = checked.filter((x) => x[id] !== item[id])
		checked.push(item)
		console.log("CHECKED:",checked)
		this.setState({checked:checked})
	}
	
	export function delChecked(item){
		/*
			del item into checked array
			checked is global variable			
		*/
		let checked = this.state.checked
		let config = this.state.config
		let id = config.filter((x) => x.col === 'id')[0].title
		checked = checked.filter((x) => x[id] !== item[id])
		console.log(checked)
		this.setState({checked:checked})
	}
	
	export function ColumnResizeEnd(e) {
		let colsSize = this.state.colsSize
		let usersettings = this.state.usersettings
		colsSize[e.column.columnKey] = e.element.clientWidth
		usersettings[this.state.path].colsSize = colsSize
		this.setState({colsSize:colsSize,usersettings:usersettings},() => {
			apishka(
				'post', 
				{settings:usersettings},
				{},
				'/api/saveusersettings',
				(i) => console.log(colsSize),
				(err) => console.log('colsSize err')
			)
		})
	}
	
	export function onColReorder(e){
		let arr = []
		let colsOrder = this.state.colsOrder
		let usersettings = this.state.usersettings
		e.columns.forEach((x) => arr.push(x.props))
		usersettings[this.state.path].colsOrder = arr
		this.setState({colsOrder:arr,usersettings:usersettings},() => {
			apishka(
				'post', 
				{settings:usersettings},
				{},
				'/api/saveusersettings',
				(i) => console.log(colsOrder),
				(err) => console.log('colsOrder err')
			)
		})
	}
